import Modal from 'react-modal';
import {useState} from 'react'
import { doc, updateDoc } from "firebase/firestore";
import { db } from '../config/firebase';

function EditTask({open, onClose, toEditTitle, toEditDescription, id}) {

  const [title, setTitle] = useState(toEditTitle)
  const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        transform: 'translate(-50%, -50%)',
    },
};
  /* function to update firestore */
  const handleUpdate = async (e) => {
    e.preventDefault()
    const taskDocRef = doc(db, 'categories', id)
    try{
      await updateDoc(taskDocRef, {
        title: title,
      })
      onClose()
    } catch (err) {
      alert(err)
    }
    
  }

  return (
    <Modal modalLable='Editar Categoria' onRequestClose={onClose} isOpen={open} style={customStyles}>
      <h5 className="modal-title" id="exampleModalLabel" >Editar categoria</h5>

      <div className='modal-body'>
        
          <form onSubmit={handleUpdate}>
            <input type='text' name='title' onChange={(e) => setTitle(e.target.value.toUpperCase())} value={title} className='form-control'/>

            <div style={{height: '30px'}}></div>

            <div className='row justify-content-between modal-footer'>

              <button className='btn btn-secondary' onClick={onClose}>Cancelar</button>
              <button type='submit' className='btn btn-warning'>Editar</button>
              </div>
          </form> 
              
      </div>
    
    </Modal>
  )
}

export default EditTask
