import Modal from 'react-modal';
import {useState} from 'react'
import {collection, addDoc, Timestamp} from 'firebase/firestore'
import uuid from "react-uuid"
import { db } from '../config/firebase';

function AddCategory({onClose, open}) {
  const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        transform: 'translate(-50%, -50%)',
    },
};
  const [title, setTitle] = useState('')

  /* function to add new task to firestore */
  const handleSubmit = async (e) => {
    e.preventDefault()
    try {
      await addDoc(collection(db, 'categories'), {
        id: uuid(),
        title: title,
        created: Timestamp.now()
      })
      onClose()
    } catch (err) {
      alert(err)
    }
  }

  return (
    <Modal contentLabel='Agregar Categoria' onRequestClose={onClose} isOpen={open} style={customStyles} ariaHideApp={false}>
      <h5 className="modal-title" id="exampleModalLabel" >Agregar categoria</h5>

      <div>
         
         <div className='modal-body'>
              <form onSubmit={handleSubmit}>
              <input 
                type='text' 
                className='form-control'
                name='title' 
                onChange={(e) => setTitle(e.target.value.toUpperCase())} 
                value={title}
                placeholder='Nombre de categoria'/>
                <div style={{height: '30px'}}>
                  
                </div>
                 <div className='row justify-content-between modal-footer'>

                 <button className='btn btn-secondary' onClick={onClose}>Cancelar</button>
                 <button type='submit' className='btn btn-success'>Guardar</button>
              </div>
            </form>
         </div>

        
       
      </div>
    </Modal>
  )
}

export default AddCategory
