import './task.css'
import {useState} from 'react'
import EditTask from './EditTask'
import { doc, deleteDoc} from "firebase/firestore";
import { Link} from "react-router-dom";
import { db } from '../config/firebase';

function Category({reference, id,title}) {

  const [open, setOpen] = useState({edit:false, view:false})

  const handleClose = () => {
    setOpen({edit:false, view:false})
  }


  /* function to delete a document from firstore */ 
  const handleDelete = async () => {
    const taskDocRef = doc(db, 'categories', reference)
    try{
      await deleteDoc(taskDocRef)
    } catch (err) {
      alert(err)
    }
  }

  return (
    <div className={`task ${true && 'task--borderColor'}`}>
    
      <div className='task__body'>
        <h2>{title}</h2>
        <div className='task__buttons'>
          <div className='task__deleteNedit'>
            <button 
              className='task__editButton' 
              onClick={() => setOpen({...open, edit : true})}>
              Edit
            </button>
            <button className='task__deleteButton' onClick={handleDelete}>Delete</button>
          </div>
          <Link to={{pathname:'/categories/'+id+'/curses/?name='+title}} className="btn btn-success">View curses </Link>
        
        </div>
      </div>

      {open.edit &&
        <EditTask 
          onClose={handleClose} 
          toEditTitle={title} 
          open={open.edit}
          id={reference} />
      }

    </div>
  )
}

export default Category