import {useEffect, useState} from 'react'
import {collection, addDoc, doc, updateDoc} from 'firebase/firestore'
import uuid from "react-uuid"
import Modal from 'react-modal';
import {db} from '../config/firebase';

export const AddQuiz = ({onClose, open,curseId, isEdit, quiz})  => {

  const [question, setQuestion] = useState('')
  const [explanation, setExplanation] = useState('')
  const [isSaving, setIsSaving] = useState(false);
  const [answers, setAnswers] = useState([
   
  ])
  const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: '10%',
        bottom: 'auto',
        transform: 'translate(-50%, -50%)',
    },
};


useEffect(() => {
    if(isEdit){
        setQuestion(quiz.question)
        setExplanation(quiz.explanation)
        setAnswers(quiz.answers)
        quiz.answers.map(e => {
            console.log(e.isAnswer)
        })
    }
  },[])

  /* function to add new task to firestore */
  const handleSubmit = async (e) => {
    e.preventDefault()
    try {
    setIsSaving(true);
        if(isEdit){
            const docRef = doc(db, 'quizzes', quiz.reference)
            await updateDoc(docRef, {
                'id': uuid(),
                'curseId': curseId,
                'question': question,
                'explanation': explanation,
                'answers': answers, 
              })
        }else{
            await addDoc(collection(db,'quizzes'),{
                'id': uuid(),
                'curseId': curseId,
                'question': question,
                'explanation': explanation,
                'answers': answers, 
            })
        }
            

    setIsSaving(false);
      onClose()
    } catch (err) {
      alert(err)
    }
  }

  const addAnswer = (e) =>{
    e.preventDefault()
    const newBlankData = {
        'answer': '',
        'isAnswer': false
    }
    setAnswers([...answers,newBlankData])
  }

  const changeAnswer = (index,e) => {
    let data = [...answers];
    data[index][e.target.name] = e.target.value
    setAnswers(data);
  }


  const changeIsAnswer = (index,e) => {
    let data = [...answers];
    data[index][e.target.name] = e.target.checked
    setAnswers(data);
  }

  const deleteAnswer = (index, e) => {
    e.preventDefault()
    let data = [...answers];
    data.splice(index, 1)
    setAnswers(data)
  }

  return (
    <Modal contentLabel='Agregar Quiz' onRequestClose={onClose} isOpen={open} style={customStyles} ariaHideApp={false}>
    <h5 className="modal-title" id="exampleModalLabel" >Agregar Quiz</h5>

    <div>
       
       <div className='modal-body'>
            <form onSubmit={handleSubmit}>

            <label htmlFor="title" className="col-form-label">Enunciado del quiz:</label>

            <textarea 
              type='text' 
              className='form-control'
              name='question' 
              rows='4'
              cols='30'
              onChange={(e) => setQuestion(e.target.value)} 
              value={question}
              placeholder='Enunciado del quiz'/>
              <div style={{height: '30px'}} required></div>

            <label htmlFor="description" className="col-form-label">Explicacion de la respuesta:</label>

            <textarea 
              type='textarea' 
              className='form-control'
              rows='4'
              cols='30'
              name='description' 
              onChange={(e) => setExplanation(e.target.value)} 
              value={explanation}
              placeholder='Explicacin de la respuesta' required/>
              
              <div style={{height: '20px'}}></div>
              <h5>Respuestas</h5>
              <button className='btn btn-primary' onClick={(e) => addAnswer(e)}>Agregar respuesta</button>
              <div style={{height: '20px'}}></div>
                <div className='row justify-content-between p-2'>
                    <h5>Texto de respuesta</h5>
                    <h5>Es la respuesta?</h5>
                    <h5>Delete</h5>
                </div>
              {
                answers.map((value,index) => {
                    return <div className='row justify-content-between p-2' key={index}>
                        <div>
                        <input type="text" name='answer' value={value.answer} placeholder={"Respuesta #"+(index+1)} className='form-control' onChange={(e) => changeAnswer(index,e)} required/>

                        </div>
                        <div className="form-check">
                                <input className="form-check-input" name='isAnswer' defaultChecked={value.isAnswer} type="checkbox" onChange={(e) => changeIsAnswer(index,e)}/>
                        </div>

                        <div>
                            <button className='btn btn-danger' onClick={(e) => deleteAnswer(index,e)}><i className="bi bi-trash"></i></button>
                        </div>
                    </div>
                })
              }
              <div style={{height: '30px'}}></div>

              <br/>              
              <br/>
                {

                isSaving &&
                
                <div className='row justify-content-center p-3'>
                  <h5>Guardando... </h5>
                  <div style={{width:'10px'}}>
                    
                  </div>
                <div className="spinner-border text-primary" role="status"> </div></div>
                }        

               <div className='row justify-content-between modal-footer'>
                    <button className='btn btn-secondary' onClick={onClose}>Cancelar</button>
                    <button type='submit' className='btn btn-success'>Guardar</button>
              </div>
          </form>
       </div>

      
     
    </div>
  </Modal>
  )
}
