import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Curses from "./curses/curses";
import Categories from "./categories/CategoryManager";
import { Curse } from "./curses/curse";
// import Notifications from "./curses/Notifications";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
	<React.StrictMode>
		<BrowserRouter>
			<Routes>
				<Route exact path='/' element={<Categories />} />
				{/* <Route path='notifications' element={<Notifications />} /> */}
				<Route path='categories/:categoryId/curses' element={<Curses />} />
				<Route
					path='categories/:categoryId/curses/:curseId'
					element={<Curse />}
				/>
			</Routes>
		</BrowserRouter>
	</React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
