import { Link } from "react-router-dom";



export const CurseCard = ({curse}) => {
    return (
        <div className="card" style={{width: '18rem'}}>
        <img src={curse.cover} className="card-img-top" alt="..."/>
        <div className="card-body">
          <h5 className="card-title">{curse.title}</h5>
          <p className="card-text d-inline-block text-truncate" style={{width: '250px'}}>{curse.description}</p>
          <Link to={{pathname:curse.id}} className="stretched-link"></Link>
        </div>
      </div> 
    );
}