import { collection, onSnapshot, query, where } from "firebase/firestore";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { db } from "../config/firebase";
import AppBar from "../header";
import AddCurse from "./addCurse";
import { CurseCard } from "./curseCard";
import { useLocation } from "react-router-dom";

function Curses() {
	let { categoryId } = useParams();
	const [openAddCurse, setOpenAddModal] = useState(false);
	const [curses, setCurses] = useState([]);
	const search = useLocation().search;
	const name = new URLSearchParams(search).get("name");

	useEffect(() => {
		const taskColRef = query(
			collection(db, "curses"),
			where("categoryId", "==", categoryId)
		);
		onSnapshot(taskColRef, (snapshot) => {
			setCurses(
				snapshot.docs.map((doc) => ({
					reference: doc.id,
					id: doc.data().id,
					title: doc.data().title,
					description: doc.data().description,
					cover: doc.data().cover,
					md: doc.data().md,
				}))
			);
		});
	}, []);
	console.log(openAddCurse);
	return (
		<div>
			<AppBar />
			<div className='flex-container p-5'>
				<h1>Cursos de {name}</h1>
				<button
					onClick={() => setOpenAddModal(true)}
					className='btn btn-primary'>
					Agregar Curso +
				</button>
			</div>
			{openAddCurse && (
				<AddCurse
					onClose={() => setOpenAddModal(false)}
					open={openAddCurse}
					categoryId={categoryId}
				/>
			)}

			<div
				className={
					openAddCurse == true
						? "none container row row-cols-lg-6 row-cols-md-4 row-cols-sm-2 row-cols-1"
						: "auto container row row-cols-lg-6 row-cols-md-4 row-cols-sm-2 row-cols-1"
				}>
				{curses.map((curse) => (
					<div key={curse.id} className='col'>
						<CurseCard curse={curse} />
					</div>
				))}
			</div>
		</div>
	);
}

export default Curses;
