import { collection, onSnapshot, query, where } from "firebase/firestore";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import AppBar from "../header";
import { db } from "../config/firebase";
import { AddQuiz } from "./addQuiz";
import { QuizCard } from "./quizCard";
import AddCurse from "./addCurse";

export const Curse = () => {
	let { curseId } = useParams();
	const [quizzes, setQuizzes] = useState([]);
	const [openEditCurse, setOpenEditModal] = useState(false);
	const [openAddQuiz, setOpenAddQuiz] = useState(false);
	const [curse, setCurse] = useState();
	const [isLoading, setIsLoading] = useState(true);
	const curseReference = query(
		collection(db, "curses"),
		where("id", "==", curseId)
	);
	const taskColRef = query(
		collection(db, "quizzes"),
		where("curseId", "==", curseId)
	);
	useEffect(() => {
		setIsLoading(true);
		onSnapshot(curseReference, (snapshot) => {
			snapshot.docs.map((doc) => {
				setCurse({
					reference: doc.id,
					id: doc.data().id,
					title: doc.data().title,
					description: doc.data().description,
					cover: doc.data().cover,
					md: doc.data().md,
				});
			});
			onSnapshot(taskColRef, (snapshot) => {
				setQuizzes(
					snapshot.docs.map((doc) => ({
						reference: doc.id,
						id: doc.data().id,
						question: doc.data().question,
						curseId: doc.data().curseId,
						answers: doc.data().answers,
						explanation: doc.data().explanation,
					}))
				);
			});
		});
		//search the quizzes of the curse

		setTimeout(function () {
			setIsLoading(false);
		}, 500);
	}, []);

	return (
		<div>
			<AppBar />
			{isLoading && (
				<div className='spinner-border text-primary' role='status'>
					{" "}
				</div>
			)}
			{isLoading === false && (
				<div className='flex-container'>
					<img
						src={curse.cover}
						className='img-fluid'
						alt='cover'
						style={{ width: "100%", height: "300px" }}
					/>

					<div className='p-5'>
						<div className='row justify-content-between'>
							<h1>Curso de {curse.title}</h1>
							<button
								className='btn btn-dark'
								onClick={() => setOpenEditModal(true)}>
								Editar Descripcin o tema
							</button>
						</div>
						<div style={{ height: "50px" }} />
						<p>{curse.description}</p>
						<div style={{ height: "50px" }} />
						<h3>Quizzes</h3>
						<div style={{ height: "50px" }} />
						<button
							onClick={() => setOpenAddQuiz(true)}
							className='btn btn-primary'>
							Agregar Quiz +
						</button>
						<div className='row  row-cols-6'>
							{quizzes.map((quiz, index) => (
								<div className='col' key={index}>
									<QuizCard quiz={quiz} index={index} />
								</div>
							))}
						</div>
					</div>
				</div>
			)}

			{openEditCurse && (
				<AddCurse
					onClose={() => setOpenEditModal(false)}
					open={openEditCurse}
					categoryId={curse.categoryId}
					isEdit={true}
					curse={curse}
				/>
			)}

			{openAddQuiz && (
				<AddQuiz
					onClose={() => setOpenAddQuiz(false)}
					open={openAddQuiz}
					curseId={curseId}
					isEdit={false}
				/>
			)}
		</div>
	);
};
