import { deleteDoc, doc } from "firebase/firestore"
import { useState } from "react"
import { db } from "../config/firebase"
import { AddQuiz } from "./addQuiz"


export const QuizCard = ({quiz,index}) => {

    const [openEditQuiz, setOpenEditQuiz] = useState(false)

    const handleDelete = async () => {
        const taskDocRef = doc(db, 'quizzes', quiz.reference)
        try{
          await deleteDoc(taskDocRef)
        } catch (err) {
          alert(err)
        }
    }

    return (
        <div>
            
            <div className="card mt-5" >
                <div className="card-body">
                    <h5 className="card-title">Quiz #{index+1}</h5>
                    <p className="card-text">{quiz.question}</p>
                </div>
                <div className="card-footer row justify-content-between m-1">
                    <button className="btn btn-warning" onClick={() => setOpenEditQuiz(true)}><i class="bi bi-pencil"></i> Editar</button>
                    <button className="btn btn-danger" onClick={handleDelete}><i class="bi bi-trash"></i> Eliminar</button>
                </div>

        </div>

        {openEditQuiz &&<AddQuiz onClose={() => setOpenEditQuiz(false)} open={openEditQuiz} curseId={quiz.curseId} isEdit={true} quiz={quiz}/>}

        </div>
       
    );
}