import { useEffect, useState } from "react";
import {
	collection,
	addDoc,
	Timestamp,
	doc,
	updateDoc,
	Firestore,
} from "firebase/firestore";
import uuid from "react-uuid";
import Modal from "react-modal";
import { db, storage } from "../config/firebase";
import {
	getDownloadURL,
	ref,
	uploadBytesResumable,
	deleteObject,
} from "firebase/storage";
// import { MyEditor } from "./MyEditor";

function AddCurse({ onClose, open, categoryId, isEdit, curse }) {
	const [title, setTitle] = useState("");
	const [description, setDescription] = useState("");
	const [md, setMd] = useState("");
	const [archivoUrl, setArchivoUrl] = useState("");

	const [image, setImage] = useState("");
	const [isUploading, setIsUpLoading] = useState(false);
	const customStyles = {
		content: {
			top: "50%",
			left: "50%",
			right: "auto",
			bottom: "auto",
			visibility: "visible",
			zindex: 100,
			transform: "translate(-50%, -50%)",
		},
	};

	useEffect(() => {
		if (isEdit) {
			setTitle(curse.title);
			setDescription(curse.description);
			setMd(curse.md);
		}
	}, []);
	/* function to add new task to firestore */
	const handleSubmit = async (e) => {
		e.preventDefault();

		try {
			setIsUpLoading(true);
			if (isEdit) {
				if (image && md) {
					let pictureRef = ref(storage, curse.cover);
					deleteObject(pictureRef).then(async () => {
						await uploadImage();
					});
					let mdRef = ref(storage, curse.md);
					deleteObject(mdRef).then(async () => {
						await uploadMd();
					});
				} else {
					const docRef = doc(db, "curses", curse.reference);
					await updateDoc(docRef, {
						title: title,
						description: description,
					});
				}
			} else {
				if (!image) {
					alert("Please choose a file first!");
				}
				if (!md) {
					alert("Please choose a file .md first!");
				} else {
					await uploadImage();
				}
			}

			setIsUpLoading(false);
			onClose();
		} catch (err) {
			alert(err);
		}
	};

	const uploadImage = async () => {
		const storageRef = ref(storage, `/files/${uuid()}.png`);
		const uploadTask = uploadBytesResumable(storageRef, image);
		uploadTask.on(
			"state_changed",
			(snapshot) => {},
			(err) => console.log(err),
			() => {
				getDownloadURL(uploadTask.snapshot.ref).then(async (url) => {
					if (isEdit) {
						const docRef = doc(db, "curses", curse.reference);
						await updateDoc(docRef, {
							title: title,
							description: description,
							cover: url,
						});
					} else {
						await addDoc(collection(db, "curses"), {
							id: uuid(),
							title: title,
							categoryId: categoryId,
							description: description,
							cover: url,
							created: Timestamp.now(),
						});
					}
				});
			}
		);
	};
	const uploadMd = async () => {
		const storageRef = ref(storage, `/files/${md}`);
		const uploadTask = uploadBytesResumable(storageRef, md);
		uploadTask.on(
			"state_changed",
			(snapshot) => {},
			(err) => console.log(err),
			() => {
				getDownloadURL(uploadTask.snapshot.ref).then(async (url) => {
					if (isEdit) {
						const docRef = doc(db, "curses", curse.reference);
						await updateDoc(docRef, {
							title: title,
							description: description,
							cover: url,
							md: url,
						});
					} else {
						await addDoc(collection(db, "curses"), {
							id: uuid(),
							title: title,
							categoryId: categoryId,
							description: description,
							cover: url,
							md: url,
							created: Timestamp.now(),
						});
					}
				});
			}
		);
	};

	const archivoHandler = async (e) => {
		const archivo = e.target.files[0];
		const storageRef = storage().ref();
		const archivoPath = storageRef.child(archivo.name);
		await archivoPath.put(archivo);
		console.log("archivo cargado:", archivo.name);
		const enlaceUrl = await archivoPath.getDownloadURL();
		setArchivoUrl(enlaceUrl);
	};
	// const submitHandler = (e) => {
	// 	const nombreArchivo = e.target.value;
	// 	const coleccionRef = db().collection("curses");
	// 	const doc = coleccionRef.doc(nombreArchivo).set({ url: archivoUrl });
	// };
	const submitHandler = async (e) => {
		e.preventDefault();
		const coleccionRef = db().collection("curses");
		const docu = await coleccionRef.set({ url: archivoUrl });
		console.log("archivo cargado:", "url:", archivoUrl);
	};
	return (
		<Modal
			contentLabel='Agregar Curso'
			onRequestClose={onClose}
			isOpen={open}
			style={customStyles}
			ariaHideApp={false}>
			<h5 className='modal-title' id='exampleModalLabel'>
				Agregar curso
			</h5>

			<div>
				<div className='modal-body'>
					<form onSubmit={handleSubmit}>
						<label htmlFor='title' className='col-form-label'>
							Titulo del curso:
						</label>

						<input
							type='text'
							className='form-control'
							name='title'
							onChange={(e) => setTitle(e.target.value)}
							value={title}
							placeholder='Nombre de curso'
						/>
						<div style={{ height: "30px" }} required></div>

						<label htmlFor='description' className='col-form-label'>
							descripcion corta del tema:
						</label>

						<textarea
							type='textarea'
							className='form-control'
							rows='4'
							cols='30'
							name='description'
							onChange={(e) => setDescription(e.target.value)}
							value={description}
							placeholder='Explicacin del curso'
							required
						/>
						{/* 
						#
						#
						 */}
						<div style={{ height: "30px" }}></div>
						<label htmlFor='md' className='col-form-label'>
							archivo .md (explicacion del tema):{" "}
						</label>
						<br />
						<input
							type='file'
							id='md'
							required={false}
							onChange={archivoHandler}
						/>
						<br />
						<br />
						<a href='#'>creador de archivos .md (descargar como markdown)</a>
						{/* 
						#
						#
						 */}
						<div style={{ height: "30px" }}></div>
						<label htmlFor='adress' className='col-form-label'>
							Cover Image:{" "}
						</label>
						<br />
						<input
							type='file'
							id='address'
							required={false}
							onChange={(e) => setImage(e.target.files[0])}
						/>
						<br />
						<br />
						{isUploading && (
							<div className='row justify-content-center p-3'>
								<h5>Guardando... </h5>
								<div style={{ width: "10px" }}></div>
								<div class='spinner-border text-primary' role='status'>
									{" "}
								</div>
							</div>
						)}
						<div className='row justify-content-between modal-footer'>
							<button className='btn btn-secondary' onClick={onClose}>
								Cancelar
							</button>
							<button type='submit' className='btn btn-success'>
								Guardar
							</button>
						</div>
					</form>
				</div>
			</div>
		</Modal>
	);
}

export default AddCurse;
